	module.exports = {"data":{"markdownRemark":{"html":"<p>早朝には、「研究」誌に「音楽と情報」という記事がありますが、これは少し面白いです。現代の科学の発展は、社会にすでに存在する現象を説明し、新しいものを見つけることを試みるために、いくつかの新しい分野と概念をますます使用しています。このタイプの記事の難しさは、本当に新しいものを発見し、古い概念を完全に議論するための新しい概念を使用しないことです。古いコンセプトを創造性のない新しいコンセプトで交換してください。</p>\n<p>オリエンタルリサーチインスティテュートへWパブリックは、社会主義市場経済は資源の配分に言及するだけでなく、資源を配分しなければならないと述べた。マルクスは変化の分布について話しており、リカルドは配分に焦点を当てている。私は数回言いましたが、私はそれを主張しなければなりません。中国は現在、「移行期」にあります。安定的になるためには、開発と安定を結びつけなければなりません。市場経済は、合法化と標準化に特に注意を払わなければならず、マクロコントロールの下で完全に発展しなければならない。中央と地方の関係は政治的統合にとって重要であり、中国の発展は統一と安定なしに議論することはできない。</p>\n<p>W公共は言った：Xiaozhiは物事を支配することに優れている、大きな知恵は人々を雇用するのが得意であり、知恵は立法では良いです。</p>\n<p>最後に、グレー経済、反腐敗、政府業務の標準化、社会主義民主主義と人権、文化問題など、オリエンタル・リサーチ・インスティテュートの基礎研究の方向性が来年決定される。対策研究が主流であり、理論研究は革新的でなければならない。それは外に雪が降っていて、雪が降っていて、空と戦っていて、楽しいです。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0011.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0011.md","frontmatter":{"title":"1月17日 月曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0011.md absPath of file >>> MarkdownRemark"}}

